:root {
  font-family: "Noto Sans JP", sans-serif;
  --font-english: "Rubik", sans-serif;
}

.l-container {
  color: gray;
  width: 100vw;
  height: 100vh;
  display: grid;
  justify-items: center;
  align-items: center;
}

.l-logo_area__image {
  width: 10rem;
  height: 10rem;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.1;
}
@media (min-width: 64rem) {
  .l-logo_area__image {
    width: 30rem;
    height: 30rem;
  }
}
@media (min-width: 77rem) {
  .l-logo_area__image {
    width: 50rem;
    height: 50rem;
  }
}
@media (min-width: 102rem) {
  .l-logo_area__image {
    width: 70rem;
    height: 70rem;
  }
}
@media (min-width: 128rem) {
  .l-logo_area__image {
    width: 100rem;
    height: 100rem;
  }
}
.l-logo_area__text {
  font-size: 1.6rem;
  text-align: center;
  font-family: var(--font-english);
}

.l-normal_container {
  --back-color: var(--color-120-light);
  max-width: min(100ch, 100%);
  margin: 0 auto;
}
.l-normal_container__content {
  width: 100%;
  height: 80vh;
  background-color: var(--back-color);
}

.l-link_list {
  --link-color: var(--color-210-light);
  --link-font-color: var(--color-210-dark);
}
.l-link_list__item {
  display: block;
  color: var(--link-font-color);
  padding: 0.5rem;
  cursor: pointer;
  font-size: 1.6rem;
  border-bottom-left-radius: 1rem;
  border-left: 0.5rem solid var(--link-color);
  border-bottom: 0.1rem solid var(--link-color);
}
.l-link_list__item:hover {
  color: white;
  background-color: var(--link-color);
  border-left: 0.5rem solid lightgray;
  border-bottom: 0.1rem solid lightgray;
}

.l-tab_list_1 {
  --border-weight: 0.2rem;
  --border-radius: 0.4rem;
  display: flex;
  background-color: white;
}
.l-tab_list_1__item {
  display: block;
  font-size: 1.6rem;
  color: lightgray;
  padding: 1rem;
  cursor: pointer;
  min-width: 10rem;
  text-align: center;
  border-top-left-radius: var(--border-radius);
  border-top-right-radius: var(--border-radius);
}
.l-tab_list_1__item:hover {
  background-color: var(--back-color);
  filter: brightness(160%);
  color: white;
}
.l-tab_list_1__item-active {
  font-weight: 900;
  color: black;
  background-color: var(--back-color);
  z-index: 100;
}
.l-tab_list_1__item-active:hover {
  background-color: var(--back-color);
  filter: brightness(100%);
  color: black;
}

